
















































import { Component, Vue } from "vue-property-decorator";
import NavigationDrawerComponent from "@/components/NavigationDrawer.component.vue";
import AppBarComponent from "@/components/AppBarComponent.vue";
import HeroComponent from "@/components/main/Hero.component.vue";
import AboutComponent from "@/components/main/About.component.vue";
import ContactComponent from "@/components/main/Contact.component.vue";
import ProjectsComponent from "@/components/main/Projects.component.vue";

@Component({
  components: {
    AppBarComponent,
    NavigationDrawerComponent,
    HeroComponent,
    AboutComponent,
    ContactComponent,
    ProjectsComponent,
  },
})
export default class MainView extends Vue {
  drawer = false;

  /**
   * Function for scrolling to the select section
   * @param elementId
   */
  private scrollTo(elementId: string): void {
    // Hide drawer
    this.drawer = false;

    // Scroll to selected position
    let element: HTMLElement | null = document.getElementById(elementId);
    element?.scrollIntoView({ behavior: "smooth", block: "start" });
  }
}
