  /**
   * Open the given link in a new window
   * @param link
   * @private
   */
  private openUrl(link: string) {
    window.open(link);
  }

  /**
   * Filters the projects by the given filter
   * @private
   */
  private get filteredProjects() {
    const filteredResults: Partial<ProjectModel>[] = [];

    this.projects.forEach((project) => {
      // Return every project if no keywords are selected
      if (!this.filterKeywordIndex) {
        filteredResults.push(project);
        return;
      }

      project.keywords?.forEach((keyword: Keyword) => {
        if (this.filterKeywordIndex == keyword) {
          filteredResults.push(project);
        }
      });
    });
    return filteredResults;
  }
}
