










































































import { Component, Vue } from "vue-property-decorator";
import CustomTitleComponent from "@/components/text/CustomTitle.component.vue";
import LinkModel from "@/models/Link.model";
import LinkButtonComponent from "@/components/link/LinkButton.component.vue";

@Component({
  components: {
    CustomTitleComponent,
    LinkButtonComponent,
  },
})
export default class ContactComponent extends Vue {
  /**
   * Body of the email
   * @private
   */
  private messageBody = "";

  /**
   * Model that indicates if the snackbar should be shown
   * @private
   */
  private showSnackbar = false;

  /**
   * Social media links
   * @private
   */
  private links: LinkModel[] = [
    {
      icon: "mdi-github",
      link: "https://github.com/werdermann",
      name: "GitHub",
    },
    {
      icon: "mdi-gitlab",
      link: "https://gitlab.com/werdermann",
      name: "Gitlab",
    },
    {
      icon: "mdi-linkedin",
      link: "https://www.linkedin.com/in/lukas-werdermann-0b77ab229/",
      name: "LinkedIn",
    },
    {
      icon: "mdi-instagram",
      link: "https://www.instagram.com/luke.wdm/",
      name: "Instagram",
    },
  ];

  /**
   * Check if the give device is an iOS device
   * ATTENTION: iOS devices can't access the clipboard
   * @private
   */
  private isIOSDevice() {
    return (
      [
        "iPad Simulator",
        "iPhone Simulator",
        "iPod Simulator",
        "iPad",
        "iPhone",
        "iPod",
      ].includes(navigator.platform) ||
      // iPad on iOS 13 detection
      (navigator.userAgent.includes("Mac") && "ontouchend" in document)
    );
  }

  /**
   * Opens the main mail program of the user
   * @private
   */
  private sendMail(): void {
    window.open(`mailto:werdermann.lukas@gmail.com?body=${this.messageBody}`);
  }

  /**
   * Copies the mail to the user's clipboard
   * @private
   */
  private copyMailToClipboard() {
    navigator.clipboard.writeText("werdermann.lukas@gmail.com");
    this.showSnackbar = true;
  }
}
